
*{
  margin: 0;
  padding: 0;
}
body{
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #111;
}

.banner{
    background-size: cover;
    height: 500px;
    color: white;
}

.content{
    padding-top: 140px;
    padding-left: 15px;
    height:190px;
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: .3rem;
}

.button{
    color: white;
    border: none;
    font-weight: 700;
    outline: none;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top:0.5rem;
    padding-bottom:0.5rem;
    background-color: rgba(51, 51, 51, .5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover{
    color: black;
    background-color: #e6e6e6;
}

.description{
    width:45rem;
    height: 80px;
    line-height: 1.3;
    padding-top: 1rem;
    max-width: 360px;
    font-size: 1rem;
}

.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),rgba(#111));
}
.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    background-color: #111;
}

.logo{
    position: fixed;
    left: 20px;
    width: 80px;
}

.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
}
.row{
    margin-left: 20px;
    color:white;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-y: hidden;
    overflow-x: scroll;
}

.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    max-height: 250px;
    margin-right: 10px;
}
.poster:hover{
    transition: .5s cubic-bezier(0,1.2,.2,1.5);
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
.smallPoster{
    max-height: 160px;
    margin-right: 10px;
}
.smallPoster:hover{
    transition: .5s cubic-bezier(0,1.2,.2,1.5);
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

